import { getToken } from "@/utils/auth";
export default {
  name: 'Layout',
  data: function data() {
    return {
      user: {}
    };
  },
  components: {
    Navbar: Navbar,
    Sidebar: Sidebar,
    AppMain: AppMain
  },
  mixins: [ResizeMixin],
